import ArrayBufferType from './ArrayBufferType';
import {getFilename} from '../../utils';

const fonts = document.fonts;

/**
 * @class FontType
 * @extends {Exo.ArrayBufferType}
 * @memberof Exo
 */
export default class FontType extends ArrayBufferType {

    /**
     * @override
     */
    get storageKey() {
        return 'font';
    }

    /**
     * @override
     */
    create(response, { addToDocument = true, family = getFilename(response.url), destriptors } = {}) {
        return super
            .create(response, null)
            .then((arrayBuffer) => new Promise((resolve, reject) => {
                const fontFace = new FontFace(family, arrayBuffer, destriptors),
                    promise = fontFace.load();

                if (addToDocument) {
                    promise.then(() => fonts.add(fontFace));
                }

                promise.then(
                    () => resolve(fontFace),
                    () => reject(fontFace),
                );
            }));
    }
}



