import EventEmitter from '../core/EventEmitter';
import ResourceContainer from './ResourceContainer';
import * as Types from './types';

/**
 * @class ResourceLoader
 * @extends {Exo.EventEmitter}
 * @memberof Exo
 */
export default class ResourceLoader extends EventEmitter {

    /**
     * @constructor
     * @param {String} [basePath='']
     */
    constructor(basePath = '') {
        super();

        /**
         * @private
         * @member {Object[]}
         */
        this._queue = [];

        /**
         * @private
         * @member {String}
         */
        this._basePath = basePath;

        /**
         * @private
         * @member {Object.<String, String>}
         */
        this._options = {
            method: 'GET',
            mode: 'cors',
            cache: 'default'
        };

        /**
         * @private
         * @member {Exo.ResourceContainer}
         */
        this._resources = new ResourceContainer();

        /**
         * @private
         * @member {Map.<String, Exo.ResourceType>}
         */
        this._types = new Map();

        /**
         * @private
         * @member {?Promise}
         */
        this._loading = null;

        /**
         * @private
         * @member {Number}
         */
        this._itemsLoaded = 0;

        this.registerTypes();
    }

    /**
     * @public
     * @readonly
     * @member {Object[]}
     */
    get items() {
        return this._resources;
    }

    /**
     * @public
     * @readonly
     * @member {Exo.ResourceContainer}
     */
    get resources() {
        return this._resources;
    }

    /**
     * @public
     * @readonly
     * @member {Number}
     */
    get itemsLoaded() {
        return this._itemsLoaded;
    }

    /**
     * @public
     * @readonly
     * @member {Boolean}
     */
    get isLoading() {
        return this._loading !== null;
    }

    /**
     * @public
     * @member {String}
     */
    get basePath() {
        return this._basePath;
    }

    set basePath(value) {
        this._basePath = value;
    }

    /**
     * @public
     * @member {Object.<String, String>}
     */
    get options() {
        return this._options;
    }

    set options(value) {
        this._options = value;
    }

    /**
     * @public
     * @chainable
     * @param {String} name
     * @param {Exo.ResourceType} type
     * @returns {Exo.ResourceLoader}
     */
    registerType(name, type) {
        this._types.set(name, type);
        this._resources.addType(name);

        return this;
    }

    /**
     * @public
     * @chainable
     * @returns {Exo.ResourceLoader}
     */
    registerTypes() {
        return this
            .registerType('arrayBuffer', new Types.ArrayBufferType())
            .registerType('audioBuffer', new Types.AudioBufferType())
            .registerType('audio', new Types.AudioType())
            .registerType('blob', new Types.BlobType())
            .registerType('font', new Types.FontType())
            .registerType('image', new Types.ImageType())
            .registerType('json', new Types.JSONType())
            .registerType('music', new Types.MusicType())
            .registerType('sound', new Types.SoundType())
            .registerType('sprite', new Types.SpriteType())
            .registerType('string', new Types.StringType())
            .registerType('texture', new Types.TextureType());
    }

    /**
     * @public
     * @returns {Promise}
     */
    load() {
        if (this._loading) {
            return this._loading;
        }

        this._itemsLoaded = 0;

        this.trigger('start', this._queue.length);

        this._loading = this._queue
            .map((item) => this.loadItem(item))
            .reduce((sequence, promise) => sequence
                .then(() => promise)
                .then((resource) => {
                    this.trigger('progress', resource, ++this._itemsLoaded, this._queue.length);
                }), Promise.resolve())
            .then(() => {
                this._queue.length = 0;

                this.trigger('complete', this._itemsLoaded);
            });

        return this._loading;
    }

    /**
     * @public
     * @param {Object} item
     * @param {String} item.type
     * @param {String} item.name
     * @param {String} item.path
     * @param {Object} item.options
     * @returns {Promise<*>}
     */
    loadItem({ type, name, path, options } = {}) {
        if (!this._types.has(type)) {
            throw new Error(`Invalid resource type "${type}".`);
        }

        if (this._resources.has(type, name)) {
            return Promise.resolve(this._resources.get(type, name));
        }

        const typeHandler = this._types.get(type),
            resourcePath = this._basePath + path,
            requestOptions = this._options;

        if (this._database) {
            return this._database
                .loadData(typeHandler.storageKey, name)
                .then((data) => data ? Promise.resolve(data) : typeHandler.request(resourcePath, requestOptions)
                    .then((source) => this._database.saveData(typeHandler.storageKey, name, source)
                        .then((source) => Promise.resolve(source))))
                .then((source) => typeHandler.create(source, options))
                .then((resource) => {
                    this._resources.set(type, name, resource);

                    return resource;
                });
        }

        return typeHandler
            .load(resourcePath, requestOptions, options)
            .then((resource) => {
                this._resources.set(type, name, resource);

                return resource;
            });
    }

    /**
     * @public
     * @chainable
     * @param {String} type
     * @param {String} name
     * @param {String} path
     * @param {Object} [options]
     * @returns {Exo.ResourceLoader}
     */
    add(type, name, path, options) {
        if (!this._types.has(type)) {
            throw new Error(`Invalid resource type "${type}".`);
        }

        this._queue.push({ type, name, path, options });

        return this;
    }

    /**
     * @public
     * @chainable
     * @param {String} type
     * @param {Map.<String, String>|Object.<String, String>} list
     * @param {Object} [options]
     * @returns {Exo.ResourceLoader}
     */
    addList(type, list, options) {
        const items = (list instanceof Map) ? list : Object.entries(list);

        for (const [name, path] of items) {
            this.add(type, name, path, options);
        }

        return this;
    }

    /**
     * @public
     * @chainable
     * @returns {Exo.ResourceLoader}
     */
    reset() {
        this._queue.length = 0;
        this._resources.clear();
        this.off();

        return this;
    }

    /**
     * @public
     */
    destroy() {
        super.destroy();

        this._queue.length = 0;
        this._queue = null;

        this._resources.destroy();
        this._resources = null;

        this._types.clear();
        this._types = null;

        this._loading = null;
    }
}



